.introduction {
    width: 80%;
    float: right;
    height: 100vh;
    color: white;
}

.carousel-control-next-icon {
    display: none !important;
}

.carousel-control-prev-icon {
    display: none !important;
}

img {
    height: 100vh;
}

.splash_pic {
    height: 100vh;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;

}

.typing_header {
    font-size: 32px;
    height: 90px;

    font-family: "Quicksand", Arial, sans-serif;

    width: 40%;
    top: 30%;
    left: 60%;
    background-color: rgb(0, 0, 0);
    /* Fallback color */
    background-color: rgba(97, 97, 97, 0.5);
    /* Black w/opacity/see-through */
    border: 3px solid #f1f1f1;
    position: absolute;
    transform: translate(-50%, -50%);
    z-index: 2;
    padding: 20px;
    text-align: center;
}

.activities {
    height: 220px;

    font-family: "Quicksand", Arial, sans-serif;
    font-size: 20px;
    text-align: left;

    width: 25%;
    top: 60%;
    left: 60%;
    background-color: rgb(0, 0, 0);
    /* Fallback color */
    background-color: rgba(97, 97, 97, 0.5);
    /* Black w/opacity/see-through */
    color: white;
    border: 3px solid #f1f1f1;
    position: absolute;
    transform: translate(-50%, -50%);
    z-index: 2;

    padding-top: 20px;
    padding-left: 24px;
}

.activities_header {
    font-family: "Quicksand", Arial, sans-serif;

    text-align: left;
    font-size: 20px;
}